body {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: #f0f0f0;
}

.watch {
  box-sizing: border-box;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80vmin;
  height: 80vmin;
  border: 5px dotted #4f94b9;
  border-radius: 50%;
}
.watch__circle {
  position: absolute;
  z-index: 1;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: orange;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
}
.watch__second-hand, .watch__minute-hand {
  position: absolute;
  transform-origin: bottom;
  animation: rotate-animation 60s linear infinite;
}
.watch__second-hand {
  width: 2px;
  background-color: green;
  height: 50%;
  top: 0;
  left: 50%;
  transform: translate(-50%, -100%);
  animation-duration: 5s;
}
.watch__minute-hand {
  width: 10px;
  background-color: blue;
  height: 25%;
  top: 25%;
  left: 49.5%;
  transform: translate(-50%, -100%);
}

@keyframes rotate-animation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}/*# sourceMappingURL=main.css.map */